<template>
<div>
    <div id="page-wrapper">

                <div class="container-fluid">
                    <div class="row bg-title">
                        <div class="col-lg-3 col-md-4 col-sm-4 col-xs-12">
                            <h4 class="page-title">LiveView</h4> 
                        </div>
                        <div class="col-lg-9 col-sm-8 col-md-8 col-xs-12">
                            <button class="right-side-toggle waves-effect waves-light btn-info btn-circle pull-right m-l-20"><i class="ti-settings text-white"></i></button>
                            <ol class="breadcrumb">
                                <li><a href="#">Live</a></li>
                                <li class="active">LiveView</li>
                            </ol>
                        </div>
                    </div>
                </div>

        <div class="row">
            <div class="col-sm-9" id="videoPanel">
                <div name='flex' class="videoColor" v-for="r in rows" :key="r">
                    <div style="flex:1; border:1px solid black;" name="flex" v-for="c in cols" @contextmenu.prevent="stopVideo($event)" @click="videoClick(r,c,$event)" :key="c">
                        <video class="h5video" v-bind:id="'h'+r+c" autoplay></video>
                    </div>
                </div>
                <div class="btn-group">
                    <button type="button" class="btn btn-default btn-outline " data-row="1|1" @click="changePanel($event)">
                        <i><img calss="layouticon" src='../../assets/img/layout/1x1.png'></i></button>
                    <button type="button" class="btn btn-default btn-outline waves-effect" data-row="2|2" @click="changePanel($event)">
                        <img calss="layouticon" src='../../assets/img/layout/2x2.png' ></button>
                    <button type="button" class="btn btn-default btn-outline waves-effect" data-row="3|3" @click="changePanel($event)">
                        <img calss="layouticon" src='../../assets/img/layout/3x3.png'></button>
                    <button type="button" class="btn btn-default btn-outline waves-effect" data-row="4|4" @click="changePanel($event)">
                        <img src='../../assets/img/layout/4x4.png'></button>
                </div>
            </div>
            <div class="col-sm-3">
                <div class="well sidebar-nav">
                <div class="box box-solid">
                    <div class="box-header with-border">
                        <h5 class="box-title">DEVICE</h5>
                        <div class="box-tools">
                        </div>
                    </div>
                    <div class="box-body no-padding pre-scrollable">
                        <div id="treeview"></div>
                    </div>
                </div>
                </div><!--/.well -->
            </div>
        </div>

    </div>

</div>
</template>   

<script>
import '../../assets/h5splayer.js'
import {H5siOS,H5sPlayerCreate} from '../../assets/h5splayerhelper.js'
import qs from 'qs'
import Vue from 'vue'
import 'patternfly-bootstrap-treeview/dist/bootstrap-treeview.min.css'
import 'patternfly-bootstrap-treeview/dist/bootstrap-treeview.min.js'
export default {
    name: "liveview",
    data() {
        return {
            rows: 3,
            cols: 3,
            selectCol: 1,
            selectRow: 1,
            map: new Map()
        };
    },
    mounted() {
        $(".content").innerHeight($('.content-wrapper').innerHeight() - $('.content-header').outerHeight() - $('.main-header').innerHeight());
        $('div[name="flex"]').height(($(".content").height() - 50) / this.rows);
        let _this = this;
        this.loadSrc();
    },
    methods: {
        loadSrc() {

            let _this =this;
            var root = process.env.API_ROOT;
            var wsroot = process.env.WS_HOST_ROOT;
            if (root == undefined){
                root = window.location.protocol + '//' + window.location.host + window.location.pathname;
            }
            if (wsroot == undefined)
            {
                wsroot = window.location.host;
            }

            $.ajax({
                type: "GET",
            
                url: root + "/api/v1/GetSrc?session="+localStorage.getItem("session"),
                dataType: "json",
                success: function(data){
                    var srcData = [];
                    var srcGroup = {nodes: []};
                    console.log("data.src", data.src, data.src.length);
                    for(var i=0; i< data.src.length; i++){
                        var item = data.src[i];
                        var newItem ={
                                token : item['strToken'],
                                text : item['strName'],
                                icon : 'mdi mdi-camcorder fa-fw'};

                        if(!item['bOnline'])
                            newItem['icon'] = 'mdi mdi-camcorder-off fa-fw';

                        if(item['nType'] == 'H5_CLOUD')
                            newItem['icon'] = 'mdi mdi-cloud-upload fa-fw';

                        srcGroup.nodes.push(newItem);
                        if ((i%10 == 0 || i == (data.src.length - 1) )
                        && (i != 0))
                        {
                            //srcGroup.text = "group1";//(i/16)* 16 + '-'  (i/16)* 16 + 16"";
                            srcGroup.text = ((Math.ceil(i/10) - 1)* 10 + 1) 
                                            + '-' + (((Math.ceil(i/10) - 1)* 10 + 1) + 9);
                            //console.log("srcGroup=========", srcGroup, i/10); 
                            srcData.push(srcGroup);
                            srcGroup = {nodes: []};
                        }
                    }

                    let options = { 
                        levels: 1, //展现级别
                        color:"#428bca",
                        expandIcon:'glyphicon glyphicon-chevron-right',
                        collapseIcon: 'glyphicon glyphicon-chevron-down',
                        nodeIcon: 'mdi mdi-view-sequential fa-fw',
                        data: srcData,
                        onNodeSelected: function (event, data) {
                            console.log(data.token);
                            if (data.token) {
                                let vid = 'h' + _this.$data.selectRow + _this.$data.selectCol;
                                console.log(vid);
                                let conf = {
                                    videoid: vid,
                                    protocol: window.location.protocol, //http: or https:
                                    host: wsroot, //localhost:8080
                                    rootpath: '/', // '/'
                                    token: data.token,
                                    hlsver: 'v1', //v1 is for ts, v2 is for fmp4
                                    session: data.session //session got from login
                                };
                                if(_this.map.has(vid))
                                {
                                   let config= _this.map.get(vid);
                                   config.disconnect();
                                  // H5sPlayerDelete(config)
                                   _this.map.delete(vid);
                                }
                                _this.map.set(vid,H5sPlayerCreate(conf)) 
                                _this.map.get(vid).connect();
                            }
                        }

                    };
                    console.log(options);
                    $('#treeview').treeview(options);
                },

                error:function(e){
                    console.log('GetSrc failed!');
                } 
        });
        },

        changePanel(event) {
            let data = $(event.target).data('row');
            let cols = data.split('|')[1];
            let rows = data.split('|')[0];
            this.map.clear();

            Object.assign(this.$data, {
                rows: parseInt(rows),
                cols: parseInt(cols)
            });
            Vue.nextTick(function () {
                $('div[name="flex"]').height(($(".content").height() - 50) / rows);
            })
        },
        videoClick(r, c, $event) {
            this.selectCol = c;
            this.selectRow = r;
            if ($($event.target).parent().hasClass('videoClickColor')) {
                $($event.target).parent().removeClass('videoClickColor');
            } else {
                $('#videoPanel div[class*="videoClickColor"]').removeClass('videoClickColor');
                $('#videoPanel>div').eq(r - 1).find('div').eq(c - 1).addClass('videoClickColor');
                $('#videoPanel>div').eq(r - 1).find('div').eq(c - 1).children(".h5video").style.opacity = "0.25";
            }
        },
        stopVideo(event){
            if(this.map.has($(event.target).attr('id')))
            {
                let config=this.map.get($(event.target).attr('id'))
                config.disconnect();
                this.map.delete(config);
                $(event.target).attr('src',"");
                //var parent = $(event.target).parent();
                //$(event.target).remove();
                //var videoHTML = '"<video class="h5video" v-bind:id=""h"+r+c" autoplay></video>"';
                //parent.append(videoHTML);
            }
        }

    }
};

    var defaultData = [
        {
        text: 'Parent 1',
        token: 'token1',
        href: '#parent1',
        
        tags: ['4'],
        nodes: [
            {
            text: 'Child 1',
            href: '#child1',
            icon: 'mdi mdi-camcorder fa-fw',
            tags: ['2'],
            nodes: [
                {
                text: 'Grandchild 1',
                href: '#grandchild1',
                tags: ['0']
                },
                {
                text: 'Grandchild 2',
                href: '#grandchild2',
                tags: ['0']
                }
            ]
            },
            {
            text: 'Child 2',
            href: '#child2',
            tags: ['0']
            }
        ]
        },
        {
        text: 'Parent 2',
        token: 'token2',
        href: '#parent2',
        tags: ['0']
        }
    ];

</script>


<style scoped>
/* div[name='flex'] {
   
} */

div[name='flex'] {
    display: flex;
    border-bottom: 0px !important;
}

div[name='flex']+[name='flex'] {
    border-left: 0px !important;
}

#videoPanel>div:nth-last-child(2) {
    border-bottom: 1px solid rgb(22, 22, 22) !important;
}

div[name="flex"]:hover {
    /*background-color: #3c8dbc;*/
    cursor: pointer;
}


video {
    height: 100%;
    width: 100%;
}

.videoClickColor {
    background-color: #616263 !important;
    opacity: 0.80;
}

.layouticon {
    height: 100%;
    width: 100%;
}


.videoColor {
    background-color: rgb(73, 74, 75) !important;
}
.h5video{
   object-fit: fill;
}

.pre-scrollable {
    max-height: 480px;
    overflow-y: scroll;
}

</style>


