<template>
<div>
        <div id="page-wrapper">
            <div class="container-fluid">
                <div class="row bg-title">
                    <div class="col-lg-3 col-md-4 col-sm-4 col-xs-12">
                        <h4 class="page-title">Dashboard</h4> </div>
                    <div class="col-lg-9 col-sm-8 col-md-8 col-xs-12">
                        <button class="right-side-toggle waves-effect waves-light btn-info btn-circle pull-right m-l-20"><i class="ti-settings text-white"></i></button>
                        <ol class="breadcrumb">
                            <li><a href="#">DashBoard</a></li>
                            <li class="active">System</li>
                        </ol>
                    </div>
            </div>
            <div class="row">
            <div class="col-lg-3 col-md-6">
            <div class="white-box">
                <h3 class="box-title">System info</h3>
                <ul class="country-state">
                    <li>
                        <h4>Version: {{information.strVersion}}</h4>
                        <div class="progress">
                            <div class="progress-bar progress-bar-success" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100" style="width:100%;"></div>
                        </div>
                    </li>
                    <li>
                        <h5>HostID: {{information.strHostId}}</h5>
                        <div class="progress">
                            <div class="progress-bar progress-bar-inverse" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100" style="width:100%;"></div>
                        </div>
                    </li>
                    <li>
                        <h5>License: {{information.strLicenseType}}</h5>
                        <div class="progress">
                            <div class="progress-bar progress-bar-info" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100" style="width:100%;"></div>
                        </div>
                    </li>
                    <li>
                        <h5>Channel: {{information.strChannelLimit}}</h5>
                        <div class="progress">
                            <div class="progress-bar progress-bar-danger" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100" style="width:100%;"></div>
                        </div>
                    </li>
                    <li>
                        <h5>Endtime: {{information.strEndtime}}</h5>
                        <div class="progress">
                            <div class="progress-bar progress-bar-info" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100" style="width:100%;"></div>
                        </div>
                    </li>

                </ul>
                 </div>
                 </div>


                    <div class="col-lg-3 col-md-6">
                        <div class="panel">
                            <div class="panel-footer">
                                <div class="row">
                                    <div class="col-xs-6">
                                        <h1 class="m-b-0 m-t-0 font-medium">{{runInfo.strCPU}}</h1>
                                        <h4 class="m-t-0">CPU</h4> </div>
                                    <div class="col-xs-6">
                                        <button type="button" class="btn mtbutton btn-success btn-circle btn-xl pull-right"><i class="icon-graph"></i></button>
                                    </div>
                                </div>
                            </div>
                            <div class="panel-footer">
                                <div class="row">
                                    <div class="col-xs-6">
                                        <h1 class="m-b-0 m-t-0 font-medium">{{runInfo.strMemory}}</h1>
                                        <h4 class="m-t-0">Memory</h4> </div>
                                    <div class="col-xs-6">
                                        <button type="button" class="btn mtbutton btn-default btn-circle btn-xl pull-right"><i class="icon-graph"></i></button>
                                    </div>
                                </div>
                            </div>
                            <div class="panel-footer">
                                <div class="row">
                                    <div class="col-xs-6">
                                        <h2 class="m-b-0 m-t-0 font-medium">{{runInfo.strRunTime}}</h2>
                                        <h4 class="m-t-0">Running</h4> </div>
                                    <div class="col-xs-6">
                                        <button type="button" class="btn mtbutton btn-primary btn-circle btn-xl pull-right"><i class="icon-speedometer"></i></button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>


                    <div class="col-lg-3 col-md-6">
                        <div class="panel">
                            <div class="panel-footer">
                                <div class="row">
                                    <div class="col-xs-6">
                                        <h1 class="m-b-0 m-t-0 font-medium">{{runInfo.strNetworkIn}}</h1>
                                        <h4 class="m-t-0">Network In</h4> </div>
                                    <div class="col-xs-6">
                                        <button type="button" class="btn mtbutton btn-success btn-circle btn-xl pull-right"><i class="icon-graph"></i></button>
                                    </div>
                                </div>
                            </div>
                            <div class="panel-footer">
                                <div class="row">
                                    <div class="col-xs-6">
                                        <h1 class="m-b-0 m-t-0 font-medium">{{runInfo.strNetworkOut}}</h1>
                                        <h4 class="m-t-0">Network Out</h4> </div>
                                    <div class="col-xs-6">
                                        <button type="button" class="btn mtbutton btn-success btn-circle btn-xl pull-right"><i class="icon-graph"></i></button>
                                    </div>
                                </div>
                            </div>
                            <div class="panel-footer">
                                <div class="row">
                                    <div class="col-xs-6">
                                        <h2 class="m-b-0 m-t-0 font-medium">{{runInfo.strFreeSpace}}</h2>
                                        <h4 class="m-t-0">Free Space</h4> </div>
                                    <div class="col-xs-6">
                                        <button type="button" class="btn mtbutton btn-default btn-circle btn-xl pull-right"><i class="icon-screen-desktop"></i></button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="col-lg-3 col-md-6">
                        <div class="panel">
                            <div class="panel-footer">
                                <div class="row">
                                    <div class="col-xs-6">
                                        <h1 class="m-b-0 m-t-0 font-medium">{{srcInfo.strSrcCnt}}</h1>
                                        <h4 class="m-t-0">Cameras</h4> </div>
                                    <div class="col-xs-6">
                                        <button type="button" class="btn mtbutton btn-info btn-circle btn-xl pull-right"><i class="icon-grid"></i></button>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="panel">
                            <div class="panel-footer">
                                <div class="row">
                                    <div class="col-xs-6">
                                        <h1 class="m-b-0 m-t-0 font-medium">{{runInfo.strTotalSpace}}</h1>
                                        <h4 class="m-t-0">Total Space</h4> </div>
                                    <div class="col-xs-6">
                                        <button type="button" class="btn mtbutton btn-default btn-circle btn-xl pull-right"><i class="icon-screen-desktop"></i></button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>





                 </div>
            </div>
            </div>
        </div>
</div>

</template>


<script>
export default {
    name: 'dashboard',
	data(){
        return{
            information:{
                strVersion: "",
                strHostId: "",
                strLicenseType: "",
                strLicenseFull: "",
                strChannelLimit: "",
                strEndtime: ""
            },
            runInfo:{
                strRunTime: "",
                strCPU: "",
                strMemory: "",
                strNetworkIn: "",
                strNetworkOut: "",
                strTotalSpace: "",
                strFreeSpace: ""
            },
            srcInfo:{
                strSrcCnt: ""
            }

        }
    },
    mounted:function(){
        let _this =this;
        this.GetSystemInfo();
        this.GetSrcInfo();
        this.GetRunInfo();
        setInterval(() => {
                            this.GetRunInfo();
                            }, 5000);
    },
    methods: {
        GetSystemInfo()
        {
            let _this =this;
            var root = process.env.API_ROOT;
            if (root == undefined){
                root = window.location.protocol + '//' + window.location.host + window.location.pathname;
            }

            $.ajax({
            type: "GET",
            
            url: root + "/api/v1/GetSystemInfo?session="+localStorage.getItem("session"),
            dataType: "json",
            success: function(data){
                    _this.information = data
                    console.log(data)
            },
            error:function(e){
                console.log('GetSystemInfo failed!');
            }
        });
        },

        GetSrcInfo()
        {
            let _this =this;
            var root = process.env.API_ROOT;
            if (root == undefined){
                root = window.location.protocol + '//' + window.location.host + window.location.pathname;
            }

            $.ajax({
            type: "GET",
            
            url: root + "/api/v1/GetSrcInfo?session="+localStorage.getItem("session"),
            dataType: "json",
            success: function(data){
                    _this.srcInfo.strSrcCnt = data.src.length;
            },
            error:function(e){
                console.log('GetSystemInfo failed!');
            }
        });
        },

        GetRunInfo()
        {
            let _this =this;
            var root = process.env.API_ROOT;
            if (root == undefined){
                root = window.location.protocol + '//' + window.location.host + window.location.pathname;
            }

            $.ajax({
            type: "GET",
            url: root + "/api/v1/GetRunInfo?session="+localStorage.getItem("session"),
            dataType: "json",
            success: function(data){
                    _this.runInfo = data
                    console.log(data)
            },
            error:function(e){
                console.log('GetRunInfo failed!');
            }
        });
        }
    }
}
</script>


