import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/views/dashboard'
import Liveview from '@/components/views/liveview'
import Playback from '@/components/views/playback'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      redirect: {name: 'dashboardRouter'}
    },
    {
      path: '/liveview',
      name: 'liveviewRouter',
      component: Liveview
    },
    {
      path: '/playback',
      name: 'playbackRouter',
      component: Playback
    },
    {
      path: '/snapshot',
      name: 'snapshotRouter',
      redirect: {name: 'dashboardRouter'}
    },
    {
      path: '/dashboard',
      name: 'dashboardRouter',
      component: Dashboard
    },
    {
      path: '/setting',
      name: 'settingRouter',
      redirect: {name: 'dashboardRouter'}
    },
    {
      path: '/cloud',
      name: 'cloudRouter',
      redirect: {name: 'dashboardRouter'}
    },
    {
      path: '/logout',
      name: 'logoutRouter',
      redirect: {name: 'dashboardRouter'}
    }
  ]
})



